.confirmation-dialog {
  min-width: 300px;
  max-width: 500px;

  h2 {
    margin: 0;
    padding: 0;
    font-size: 20px;
    font-weight: 600;
  }
}

mat-dialog-content {
  padding: 16px 24px;

  .confirmation-message {
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    color: var(--text-primary, #333);
    white-space: pre-wrap;
    word-break: break-word;
  }
}

mat-dialog-actions {
  padding: 8px 24px 16px;
  gap: 8px;
  
  button {
    min-width: 80px;
  }
}
